.slider {
  display: flex;
  animation: slide 15s infinite;
  overflow-x: auto;
}

@keyframes slide {
  0% {
    transform: translateX(0);
  }
  14.29% {
    transform: translateX(0);
  }
  20% {
    transform: translateX(calc(-100% - 20px));
  }
  34.29% {
    transform: translateX(calc(-100% - 20px));
  }
  40% {
    transform: translateX(calc(-200% - 40px));
  }
  54.29% {
    transform: translateX(calc(-200% - 40px));
  }
  60% {
    transform: translateX(calc(-300% - 60px));
  }
  74.29% {
    transform: translateX(calc(-300% - 60px));
  }
  80% {
    transform: translateX(calc(-400% - 80px));
  }
  94.29% {
    transform: translateX(calc(-400% - 80px));
  }
  100% {
    transform: translateX(0);
  }
}



